import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { ThemeProvider } from 'styled-components'

import api from '../../services/api'
import Colors from '../../styles/colors'

import Loading from '../../components/Loading'
import {
   Container,
   LinksContainer,
   QuestionContainer,
   QuestionInfos,
   QuestionNumber,
   QuestionID,
   Statement,
   AlternativesList,
   AlternativeItem,
   AlternativeContent,
} from './styles'

export default class Question extends Component {
   state = {
      loading: true,
      question: {},
      exam: {},
      theme: {
         primary: '#000',
      },
   }

   async componentDidMount() {
      const { exam, year, question } = this.props.match.params

      var exam_info = localStorage.getItem(`@exams/${exam.toLowerCase()}`)
      if (!exam_info) {
         const exam_response = await api.get(
            `/exams/${exam.toLowerCase()}.json`
         )

         exam_info = JSON.stringify(exam_response.data)

         localStorage.setItem(`@exams/${exam.toLowerCase()}`, exam_info)
      }

      const response = await api.get(
         `/exams/${exam.toLowerCase()}/questions.json?orderBy=%22id%22&equalTo=%22${question}%22`
      )

      this.setState({
         loading: false,
         question: Object.values(response.data)[0],
         exam: { ...JSON.parse(exam_info), year, id: exam },
         theme: {
            primary: exam === 'enade' ? Colors.enade : Colors.poscomp,
         },
      })
   }

   render() {
      const { loading, question, exam, theme } = this.state

      if (loading) {
         return <Loading />
      }

      return (
         <ThemeProvider theme={theme}>
            <Container>
               <LinksContainer>
                  <Link to="/">Home</Link>
                  <span> / </span>
                  <Link to={`/${exam.id}`}>{exam.name}</Link>
                  <span> / </span>
                  <Link to={`/${exam.id}/provas`}>Provas</Link>
                  <span> / </span>
                  <Link to={`/${exam.id}/questoes/${exam.year}`}>
                     {exam.year}
                  </Link>
                  <span> / </span>
                  <Link to={`/${exam.id}/questoes/${exam.year}/`}>
                     Questão {question.number}
                  </Link>
               </LinksContainer>
               <QuestionContainer>
                  <QuestionInfos>
                     <QuestionNumber>Questão {question.number}</QuestionNumber>
                     <QuestionID>#{question.id}</QuestionID>
                  </QuestionInfos>
                  {question.statement.map(statement => (
                     <Statement
                        type={statement.type}
                        dangerouslySetInnerHTML={{
                           __html: statement.content,
                        }}
                     />
                  ))}
                  <AlternativesList>
                     {question.alternatives.map(alternative => (
                        <AlternativeItem key={alternative.id}>
                           <label
                              checked={alternative.letter === question.answer}
                           >
                              <input
                                 type="radio"
                                 value={alternative.letter}
                                 checked={
                                    alternative.letter === question.answer
                                 }
                                 onChange={this.handleOnChange}
                                 disabled
                              />
                              <strong>{alternative.letter})</strong>
                              <AlternativeContent
                                 dangerouslySetInnerHTML={{
                                    __html: alternative.content,
                                 }}
                              />
                           </label>
                        </AlternativeItem>
                     ))}
                  </AlternativesList>
               </QuestionContainer>
            </Container>
         </ThemeProvider>
      )
   }
}
