import React, { Component } from 'react'
import { MdNavigateBefore, MdNavigateNext } from 'react-icons/md'
import { FaFileExport } from 'react-icons/fa'

import api from '../../services/api'

import {
   Container,
   SimHeader,
   ButtonEndSim,
   QuestionContainer,
   Back,
   Next,
   QuestionContent,
   AlternativeList,
   AlternativeItem,
   StatementContainer,
   TexContainer,
   TexText,
} from './styles'

export default class Exam extends Component {
   state = {
      selectedOption: '',
      loading: true,
      questions: [],
      currentQuestion: 0,
   }

   async componentDidMount() {
      const response = await api.get(`/questions.json`)

      this.setState({
         questions: response.data,
         loading: false,
      })
   }

   handleOptionChange = e => {
      this.setState({
         selectedOption: e.target.value,
      })
   }

   handleChangeQuestion = value => {
      const { currentQuestion, questions } = this.state
      const newValue = currentQuestion + value

      if (newValue >= 0 && newValue < questions.length) {
         this.setState({ currentQuestion: newValue })
      }
   }

   render() {
      const { selectedOption, currentQuestion, questions, loading } = this.state

      const question = questions[currentQuestion]

      if (loading) {
         return <h1>Carregando...</h1>
      }

      return (
         <Container>
            <SimHeader>
               <h1>Simulado</h1>
               <div>
                  <p>XX/{questions.length}</p>
                  <ButtonEndSim>
                     <FaFileExport size={16} color="#FFF" />
                     FINALIZAR SIMULADO
                  </ButtonEndSim>
               </div>
            </SimHeader>

            <QuestionContainer>
               <header>
                  <Back onClick={() => this.handleChangeQuestion(-1)}>
                     <MdNavigateBefore size={24} />
                     Anterior
                  </Back>
                  <p>Questão {question.number}</p>
                  <Next onClick={() => this.handleChangeQuestion(1)}>
                     Próxima
                     <MdNavigateNext size={24} />
                  </Next>
               </header>
               <QuestionContent>
                  <span>(POSCOMP/{question.year})</span>
                  {question.statement.map(item =>
                     item.type === 3 ? (
                        <p>Imagem</p>
                     ) : (
                        <StatementContainer type={item.type}>
                           <TexContainer>
                              <TexText text={item.content} />
                           </TexContainer>
                        </StatementContainer>
                     )
                  )}
               </QuestionContent>
               <AlternativeList>
                  {question.alternatives.map(alternative => (
                     <AlternativeItem key={alternative.letter}>
                        <label>
                           <input
                              type="radio"
                              value={alternative.letter}
                              checked={selectedOption === alternative.letter}
                              onChange={this.handleOptionChange}
                           />
                           <strong>{alternative.letter})</strong>
                           {/* <span>{alternative.content}</span> */}
                           <TexContainer>
                              <TexText text={alternative.content} />
                           </TexContainer>
                        </label>
                     </AlternativeItem>
                  ))}
               </AlternativeList>
            </QuestionContainer>
         </Container>
      )
   }
}
