import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { ThemeProvider } from 'styled-components'

import Colors from '../../styles/colors'
import api from '../../services/api'

import Logo from '../../assets/images/logo_petcc.svg'
import Loading from '../../components/Loading'
import {
   Container,
   TextContainer,
   ExamContainer,
   ExamList,
   ExamItem,
   ButtonMoreExams,
} from './styles'

export default class Exam extends Component {
   state = {
      loading: true,
      exam: {},
      years: ['2019', '2018', '2017'],
      theme: {
         primary: '#000',
      },
   }

   async componentDidMount() {
      const { exam } = this.props.match.params

      if (exam !== 'enade' && exam !== 'poscomp') {
         this.props.history.push('/')
      }

      document.title = `PET-CC - ${exam.toUpperCase()}`

      const response = await api.get(`/exams/${exam}.json`)

      const occurrences = response.data.occurrences.sort(
         (a, b) => a.year - b.year
      )

      this.setState({
         exam: {
            ...response.data,
            id: exam,
            last_occurrences: occurrences.slice(-3).reverse(),
         },
         loading: false,
         theme: {
            primary: exam === 'enade' ? Colors.enade : Colors.poscomp,
         },
      })
   }

   render() {
      const { exam, years, theme, loading } = this.state

      if (loading) {
         return <Loading />
      }

      console.log(exam)

      return (
         <ThemeProvider theme={theme}>
            <Container>
               <TextContainer>
                  <h2>{exam.name}</h2>
                  <strong>Provas Anteriores</strong>
                  <p>{exam.description}</p>
               </TextContainer>
               <ExamContainer>
                  <ExamList>
                     {exam.last_occurrences.map(item => (
                        <li key={item.year}>
                           <ExamItem>
                              <span>{item.year}</span>
                              <Link to={`/${exam.id}/questoes/${item.year}`}>
                                 Ver prova
                              </Link>
                           </ExamItem>
                        </li>
                     ))}
                  </ExamList>
                  <ButtonMoreExams to={`/${exam.id}/provas`}>
                     Outras Provas
                  </ButtonMoreExams>
               </ExamContainer>
            </Container>
         </ThemeProvider>
      )
   }
}
