import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { ThemeProvider } from 'styled-components'
import { FaDownload } from 'react-icons/fa'

import api from '../../services/api'
import Colors from '../../styles/colors'

import Loading from '../../components/Loading'
import {
   Container,
   LinksContainer,
   ExamsList,
   ButtonsContainer,
   TextContainer,
} from './styles'

export default class Exams extends Component {
   state = {
      loading: true,
      exam: {},
      theme: {
         primary: '#000',
      },
   }

   async componentDidMount() {
      const { exam } = this.props.match.params
      if (exam !== 'enade' && exam !== 'poscomp') {
         this.props.history.push('/sim')
      }

      const response = await api.get(`/exams/${exam}.json`)
      const occurrences = response.data.occurrences.reverse()

      this.setState({
         loading: false,
         exam: {
            ...response.data,
            occurrences,
            id: exam,
         },
         theme: {
            primary: exam === 'enade' ? Colors.enade : Colors.poscomp,
         },
      })
   }

   render() {
      const { loading, exam, theme } = this.state

      if (loading) {
         return <Loading />
      }

      return (
         <ThemeProvider theme={theme}>
            <Container>
               <LinksContainer>
                  <Link to="/">Home</Link>
                  <span> / </span>
                  <Link to={`/${exam.id}`}>{exam.name}</Link>
                  <span> / </span>
                  <Link to={`/${exam.id}/provas`}>Provas</Link>
               </LinksContainer>
               <p>Provas</p>
               <ExamsList>
                  {exam.occurrences.map(occurrence => (
                     <li key={occurrence.year}>
                        <TextContainer>
                           <span>Prova</span>
                           <Link to={`/${exam.id}/questoes/${occurrence.year}`}>
                              {exam.owner} - {occurrence.year} - {exam.name} -{' '}
                              {exam.full_name}
                           </Link>
                        </TextContainer>
                        <ButtonsContainer>
                           <button>Resolva</button>
                           <a href="#">
                              <FaDownload size={24} color="#000" />
                           </a>
                        </ButtonsContainer>
                     </li>
                  ))}
               </ExamsList>
            </Container>
         </ThemeProvider>
      )
   }
}
