import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { ThemeProvider } from 'styled-components'

import api from '../../services/api'
import Colors from '../../styles/colors'

import { MdFormatListBulleted, MdFormatListNumbered } from 'react-icons/md'
import { FaRegFileAlt, FaPencilAlt, FaQuestion } from 'react-icons/fa'
import Loading from '../../components/Loading'
import {
   Container,
   LinksContainer,
   ExamInfoContainer,
   DownloadContainer,
   FilesContainer,
   FileItem,
   SolveButton,
   ExamInfo,
   TextInfo,
   QuestionsContainer,
   HeaderQustions,
   QuestionsNumber,
   QuestionsList,
   InfoQuestionContainer,
   ButtonViewQuestion,
   ButtonViewQuestionContent,
} from './styles'

export default class Questions extends Component {
   state = {
      loading: true,
      exam: {},
      questions: [],
      theme: {
         primary: '#000',
      },
   }

   componentDidMount = async () => {
      const { exam, year } = this.props.match.params

      const { data } = await api.get(`/exams/${exam}.json`)

      const questions = Object.values(data.questions).filter(
         question => question.year === year
      )

      this.setState({
         loading: false,
         questions: questions,
         exam: {
            ...data,
            id: exam,
            year,
         },
         theme: {
            primary: exam === 'enade' ? Colors.enade : Colors.poscomp,
         },
      })
   }

   render() {
      const { theme, loading, exam, questions } = this.state

      if (loading) {
         return <Loading />
      }

      return (
         <ThemeProvider theme={theme}>
            <Container>
               <LinksContainer>
                  <Link to="/">Home</Link>
                  <span> / </span>
                  <Link to={`/${exam.id}`}>{exam.name}</Link>
                  <span> / </span>
                  <Link to={`/${exam.id}/provas`}>Provas</Link>
                  <span> / </span>
                  <Link to={`/${exam.id}/questoes/${exam.year}`}>
                     {exam.year}
                  </Link>
               </LinksContainer>
               <ExamInfoContainer>
                  <DownloadContainer>
                     <span>BAIXE OS ARQUIVOS</span>
                     <FilesContainer>
                        <FileItem to="#">
                           <MdFormatListBulleted size={24} color="#000" />
                           <span>PROVA</span>
                        </FileItem>
                        <FileItem to="#">
                           <MdFormatListNumbered size={24} color="#000" />
                           <span>GABARITO</span>
                        </FileItem>
                        <FileItem to="#">
                           <FaRegFileAlt size={24} color="#000" />
                           <span>EDITAL</span>
                        </FileItem>
                     </FilesContainer>
                     <SolveButton>Resolver Prova</SolveButton>
                  </DownloadContainer>
                  <ExamInfo>
                     <TextInfo>
                        <strong>Concurso</strong>
                        <span>
                           {exam.name} - {exam.year}
                        </span>
                     </TextInfo>
                     <TextInfo>
                        <strong>Prova</strong>
                        <span>{exam.full_name}</span>
                     </TextInfo>
                     <TextInfo>
                        <strong>Organizador</strong>
                        <span>{exam.owner}</span>
                     </TextInfo>
                  </ExamInfo>
               </ExamInfoContainer>
               <QuestionsContainer>
                  <HeaderQustions>
                     <QuestionsNumber>
                        {questions.length === 0
                           ? 'Nenhuma questão cadastrada'
                           : `${questions.length} Questões do ${exam.name}`}
                     </QuestionsNumber>
                  </HeaderQustions>
                  {!!questions.length && (
                     <QuestionsList>
                        {questions.map(question => (
                           <li key={question.id}>
                              <h3>Questão {question.number}</h3>
                              <InfoQuestionContainer>
                                 <div>
                                    <FaPencilAlt size={12} />
                                    <p>{question.area}</p>
                                 </div>
                                 <div>
                                    {' '}
                                    <FaQuestion size={12} />
                                    <p>Resolvida 100x</p>
                                 </div>
                              </InfoQuestionContainer>
                              <ButtonViewQuestion>
                                 <ButtonViewQuestionContent
                                    to={`/${exam.id}/questoes/${question.year}/${question.id}`}
                                 >
                                    Ver Questão
                                 </ButtonViewQuestionContent>
                              </ButtonViewQuestion>
                           </li>
                        ))}
                     </QuestionsList>
                  )}
               </QuestionsContainer>
            </Container>
         </ThemeProvider>
      )
   }
}
